.landing-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem;
}

.landing-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.landing-header h1 {
  color: white;
  font-size: 2rem;
  font-weight: 700;
}

.currency-toggle {
  display: flex;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 4px;
}

.currency-toggle button {
  background: transparent;
  border: none;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.currency-toggle button.active {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}

.currency-toggle button:hover {
  background: rgba(255, 255, 255, 0.1);
}

.landing-main {
  max-width: 800px;
  margin: 0 auto;
}

.related-links-section {
  margin-top: 2rem;
}

.related-links-section h3 {
  color: white;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.related-links-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.related-link-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.related-link-card:hover {
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.15);
}

.related-link-card h4 {
  color: white;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}

.related-link-card p {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.related-link-card a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  display: inline-block;
  transition: all 0.3s ease;
}

.related-link-card a:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

@media (max-width: 768px) {
  .landing-container {
    padding: 1rem;
  }

  .landing-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .landing-header h1 {
    font-size: 1.5rem;
  }

  .related-links-grid {
    grid-template-columns: 1fr;
  }
}
