.domain-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 
    0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.domain-card:hover {
  transform: translateY(-4px);
  box-shadow: 
    0 25px 30px -5px rgba(0, 0, 0, 0.15),
    0 15px 15px -5px rgba(0, 0, 0, 0.06);
}

.domain-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e2e8f0;
}

.domain-name {
  font-size: 2rem;
  font-weight: 700;
  color: #1a202c;
  margin: 0;
}

.domain-status {
  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 600;
}

.domain-info {
  margin-bottom: 2rem;
}

.price-section {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  text-align: center;
}

.price-label {
  display: block;
  font-size: 0.875rem;
  opacity: 0.9;
  margin-bottom: 0.5rem;
}

.price-value {
  font-size: 2.5rem;
  font-weight: 700;
  display: block;
}

.domain-details {
  display: grid;
  gap: 0.75rem;
}

.domain-details p {
  margin: 0;
  color: #4a5568;
  font-size: 0.95rem;
}

.domain-details strong {
  color: #2d3748;
  font-weight: 600;
}

.contact-btn {
  width: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.contact-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.4);
}

@media (max-width: 768px) {
  .domain-card {
    padding: 1.5rem;
  }

  .domain-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .domain-name {
    font-size: 1.5rem;
  }

  .price-value {
    font-size: 2rem;
  }
}
